<template>
  <div class="goodsView">
    <el-row>
      <el-col :span="6" v-for="good in SingleGoods" :key="good.id" class="goodsEle">
        <el-card shadow="hover">
          <img :src="good.src" class="image" style="width:150px;height:150px">
          <div>
            <span class="price">￥{{good.price}}</span>
            <span class="goodName">{{good.name}}</span>
            <div class="bottom clearfix">
              <span class="rate">评分：</span>
              <el-rate v-model="good.assess" isabled show-score text-color="#ff9900">
              </el-rate>
              <el-button type="danger" icon="el-icon-plus" class="GoddsCart" @click="add(good)"></el-button>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>
<script>
  export default{
    data(){
      return {
          SingleGoods:[
            {goodsId:'133',name:'肉酱满溢堡',src:'http://shb.lemdays.com/uploads/allimg/180419/1_180419152631_1.png', assess:2,price:25},
            {goodsId:'134',name:'照烧凤梨堡',src:'http://shb.lemdays.com/uploads/allimg/180419/1_180419150105_1.png', assess:2,price:20},
            {goodsId:'117',name:'香辣鸡腿',src:'http://shb.lemdays.com/pic/images/svc-mid-autumn-pool-20160630121800.png', assess:1,price:30},
            {goodsId:'116',name:'超级芝士堡',src:'http://shb.lemdays.com/uploads/allimg/180419/1_180419154053_1.png', assess:2,price:22},
            {goodsId:'117',name:'香辣鸡腿',src:'http://shb.lemdays.com/uploads/allimg/180419/1_180419140105_1.png', assess:2,price:12},
            {goodsId:'135',name:'墨西哥劲辣堡',src:'http://shb.lemdays.com/uploads/allimg/180419/1_180419153856_1.png', assess:3,price:15},
            {goodsId:'136',name:'黑胡椒鸡柳堡',src:'http://shb.lemdays.com/uploads/allimg/180419/1_180419154023_1.png', assess:2,price:22},
            {goodsId:'137',name:'老北京鸡肉卷',src:'http://shb.lemdays.com/uploads/allimg/180419/1_180419153952_1.png', assess:2,price:12},
            {goodsId:'138',name:'嫩汁鸡柳堡-蜂蜜',src:'http://shb.lemdays.com/uploads/allimg/180419/1_180419152920_1.png', assess:3,price:15},
          ]
      }
    },
    methods:{
       add:function(good){
        var goodsList = {
          goodsId:good.goodsId,
          name:good.name,
          src:good.src,
          assess:good.assess,
          price:good.price
        };
        this.$emit('addList',goodsList);
      }
    }
  }
</script>
<style>
.goodsEle {
 margin-left: 50px;
  margin-top: 20px;
}
.goodsView{
  height: 620px;
  overflow: auto;
}
.rate{
  color:#ff9900;
}
.clearfix span{
display: inline-block;
}
.goodName {
  margin-left: 30px;
  color: #f56c6c;
}
.price{
   margin-left: 40px;
   color: red;
}
.GoddsCart{
  padding: 0 !important;
  float: right;
}

</style>



