<template>
  <div class="goodsView">
    <el-row>
      <el-col :span="6" v-for="good in RecommedGoods" :key="good.id" class="goodsEle">
        <el-card shadow="hover">
          <img :src="good.src" class="image" style="width:150px;height:150px">
          <div>
            <span class="price">￥{{good.price}}</span>
            <span class="goodName">{{good.name}}</span>
            <div class="bottom clearfix">
              <span class="rate">评分：</span>
              <el-rate v-model="good.assess" isabled show-score text-color="#ff9900">
              </el-rate>
              <el-button type="danger" icon="el-icon-plus" class="GoddsCart" @click="add(good)"></el-button>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>
<script>
  export default{
    data(){
      return {
          RecommedGoods:[
            {goodsId:'139',name:'珍珠奶茶',src:'http://shb.lemdays.com/uploads/allimg/180419/1_180419130842_1.png', assess:2,price:25},
            {goodsId:'140',name:'香芋冰冰乐',src:'http://shb.lemdays.com/uploads/allimg/180419/1_180419130913_1.png', assess:2,price:20},
            {goodsId:'141',name:'香醇玫瑰饮',src:'http://shb.lemdays.com/uploads/allimg/180419/1_180419130935_1.png', assess:1,price:30},
            {goodsId:'142',name:'鲜醇豆浆',src:'http://shb.lemdays.com/uploads/allimg/180419/1_180419130957_1.png', assess:2,price:22},
            {goodsId:'143',name:'甜筒冰淇淋',src:'http://shb.lemdays.com/uploads/allimg/180419/1_180419131117_1.png', assess:2,price:12},
            {goodsId:'144',name:'摩卡冰冰乐',src:'http://shb.lemdays.com/uploads/allimg/180419/1_180419134633_1.png', assess:3,price:15},
            {goodsId:'145',name:'可乐-雪碧冰冰乐',src:'http://shb.lemdays.com/uploads/allimg/180419/1_180419134802_1.png', assess:2,price:22},
            {goodsId:'146',name:'健康果昔（芒果）',src:'http://shb.lemdays.com/uploads/allimg/180419/1_180419134912_1.png', assess:2,price:12},
            {goodsId:'147',name:'健康果昔（菠萝',src:'http://shb.lemdays.com/uploads/allimg/180419/1_180419135011_1.png', assess:3,price:15},
          ]
      }
    },
    methods:{
       add:function(good){
        var goodsList = {
          goodsId:good.goodsId,
          name:good.name,
          src:good.src,
          assess:good.assess,
          price:good.price
        };
        this.$emit('addList',goodsList);
      }
    }
  }
</script>
<style>
.goodsEle {
 margin-left: 50px;
  margin-top: 20px;
}
.goodsView{
  height: 620px;
  overflow: auto;
}
.rate{
  color:#ff9900;
}
.clearfix span{
display: inline-block;
}
.goodName {
  margin-left: 30px;
  color: #f56c6c;
}
.price{
   margin-left: 40px;
   color: red;
}
.GoddsCart{
  padding: 0 !important;
  float: right;
}

</style>



