<template>
  <div class="goodsView">
    <el-row>
      <el-col :span="6" v-for="good in RefreshmentsGoods" :key="good.id"  class="goodsEle">
        <el-card shadow="hover">
          <img :src="good.src" class="image" style="width:150px;height:150px">
          <div>
            <span class="price">￥{{good.price}}</span>
            <span class="goodName">{{good.name}}</span>
            <div class="bottom clearfix">
              <span class="rate">评分：</span>
              <el-rate v-model="good.assess" isabled show-score text-color="#ff9900">
              </el-rate>
              <el-button type="danger" icon="el-icon-plus" class="GoddsCart" @click="add(good)"></el-button>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>
<script>
  export default{
    data(){
      return {
          RefreshmentsGoods:[
            {goodsId:'124',name:'星星培根薯',src:'http://shb.lemdays.com/uploads/allimg/180419/1_180419140040_1.png', assess:2,price:25},
            {goodsId:'125',name:'香辣鸡翅',src:'http://shb.lemdays.com/uploads/allimg/180419/1_180419140216_1.png', assess:2,price:20},
            {goodsId:'126',name:'美式薯条',src:'http://shb.lemdays.com/uploads/allimg/180419/1_180419140844_1.png', assess:1,price:30},
            {goodsId:'127',name:'田园沙拉',src:'http://shb.lemdays.com/uploads/allimg/180419/1_180419140541_1.png', assess:2,price:22},
            {goodsId:'128',name:'海苔鲽鱼柳',src:'http://shb.lemdays.com/uploads/allimg/180419/1_180419140933_1.png', assess:2,price:12},
            {goodsId:'129',name:'奥尔良烤翅',src:'http://shb.lemdays.com/uploads/allimg/180419/1_180419141112_1.png', assess:3,price:15},
            {goodsId:'130',name:'香芋地瓜丸',src:'http://shb.lemdays.com/uploads/allimg/180419/1_180419140131_1.png', assess:2,price:22},
            {goodsId:'131',name:'香脆鸡米花',src:'http://shb.lemdays.com/uploads/allimg/180419/1_180419140257_1.png', assess:2,price:12},
            {goodsId:'132',name:'菠萝派',src:'http://shb.lemdays.com/uploads/allimg/180419/1_180419141039_1.png', assess:3,price:15},
          ]
      }
    },
    methods:{
       add:function(good){
        var goodsList = {
          goodsId:good.goodsId,
          name:good.name,
          src:good.src,
          assess:good.assess,
          price:good.price
        };
        this.$emit('addList',goodsList);
      }
    }
  }
</script>
<style>
.goodsEle {
 margin-left: 50px;
  margin-top: 20px;
}
.goodsView{
  height: 620px;
  overflow: auto;
}
.rate{
  color:#ff9900;
}
.clearfix span{
display: inline-block;
}
.goodName {
  margin-left: 30px;
  color: #f56c6c;
}
.price{
   margin-left: 40px;
   color: red;
}
.GoddsCart{
  padding: 0 !important;
  float: right;
}

</style>



