<template>
  <div class="router">
    <div class="subregion">
      <ul>
        <li  class="active" @click="addStyle($event)">
          <router-link to="/hot">热卖区</router-link>
        </li>
        <li @click="addStyle($event)">
          <router-link to="/single">单点区</router-link>
        </li>
        <li @click="addStyle($event)">
          <router-link to="/sweetmeat">甜食区</router-link>
        </li>
        <li @click="addStyle($event)">
          <router-link to="/refreshments">小食区</router-link>
        </li>
        <li @click="addStyle($event)">
          <router-link to="recommend">推荐区</router-link>
        </li>
      </ul>
    </div>
    <div class="displayGoods">
        <transition name="fade">
          <router-view @addList="add"></router-view>
        </transition>
    </div>
  </div>

</template>
<script>
  export default{
    data(){
      return {
        goodsList:[]
      }
    },
    props:['Goods'],
    methods:{
      add(good){
        var isPush=true;
        var goods = {
            goodsId:good.goodsId,
            name:good.name,
            src:good.src,
            assess:good.assess,
            price:good.price
        };
          goods.count = 1;
        for(var i=0,len=this.Goods.length;i<len;i++){
          if(this.Goods[i].goodsId == goods.goodsId){
            this.Goods[i].count++;
            isPush=false;
          }
        }
        if(isPush){
          this.Goods.push(goods);
        }
      },
      addStyle:function(event){
       for(var i=0,len=event.target.parentNode.parentNode.children.length;i<len;i++){
        var li = event.target.parentNode.parentNode.children[i];
          li.style.backgroundColor="#1D8ce0";
       }
      event.target.parentNode.style.backgroundColor="#409EFF"
      }
    }
  }
</script>
<style scoped>
.subregion{
  width: 100%;
  height: 40px;
  background:#1D8ce0;
}
.subregion ul{
  padding: 0;
  margin: 0;
  height: 40px;
  width: 800px;
}
.subregion li {
  list-style: none;
  float: left;
  margin-left: 30px;
  padding: 0 20px;
  line-height: 40px;
  text-align: center;
}
.active{
   background:#409EFF;
}
.subregion a{
  font-weight:600;
  color: #777;
}
.fade-enter,.fade-leave {
  opacity: 0;
}
.fade-enter-active,.fade-enter-active{
  transition: 2s all ease;
}
</style>



