<template>
  <div class="Pos">
    <el-row>
        <el-col :span="9" class="pos-order" id="pos-order">
          <el-tabs type="border-card" stripe>
              <el-tab-pane label="已点商品">
                <el-table :data="GoodsList"  class="tableGoods">
                  <el-table-column prop="name" label="商品">
                    商品
                  </el-table-column>
                  <el-table-column prop="price" label="金额">
                    金额
                  </el-table-column>
                  <el-table-column prop="count" label="数量">
                    数量
                  </el-table-column>
                  <el-table-column label="操作">
                    <template scope="scope">
                        <el-button type="danger" icon="el-icon-minus" circle class="do" @click="reduce(scope.row)"></el-button>
                        <el-button type="primary" icon="el-icon-plus" circle class="do" @click="add(scope.row)"></el-button>
                        <el-button type="danger" icon="el-icon-delete" circle class="do" @click="amputate(scope.row)"></el-button>
                    </template>
                  </el-table-column>
                </el-table>
              </el-tab-pane>
               <el-tab-pane label="已删除商品">

                <el-table :data="deletedGoods"  class="tableGoods">
                  <el-table-column prop="name" label="商品">
                    商品
                  </el-table-column>
                  <el-table-column prop="price" label="金额">
                    金额
                  </el-table-column>
                  <el-table-column prop="count" label="数量">
                    数量
                  </el-table-column>
                  <el-table-column prop="assess" label="评价">
                    评价
                  </el-table-column>
                </el-table>
              </el-tab-pane>
               <el-tab-pane label="挂单商品">
                等待开发
              </el-tab-pane>
              <el-tab-pane label="外卖">
                等待开发
              </el-tab-pane>
          </el-tabs>
          <div class="payFor">
            <el-popover
                ref="popover"
                placement="right"
                :title="账单"
                width="400"
                trigger="click"
               >
                <span>￥：{{goodsCount}}</span>
            </el-popover>
            <el-button type="primary" v-popover:popover>下单结账</el-button>
            <el-button type="danger" @click="delAllGoods">全部删除</el-button>
          </div>
          <div>
             <span class="GoodsDdetail">数量：{{goodsNumber}} 总计：{{goodsCount}}</span>
          </div>
        </el-col>
        <el-col :span="15" id="pos-listGoods">
          <NavRouter :Goods="GoodsList"></NavRouter>
        </el-col>
    </el-row>
 </div>
</template>
<script>
  import NavRouter from './NavRouter'
export default {
  name:'NavLeft',
  data(){
    return {
      GoodsList:[],
      goodsNumber:0,
      goodsCount:0,
      deletedGoods:[]
    };
  },
  methods:{
    add:function(goods){
      for(var i=0,len=this.GoodsList.length;i<len;i++){
        if(this.GoodsList[i].goodsId == goods.goodsId){
          this.GoodsList[i].count++;
        }
      }
    },
    amputate:function(goods){
      for(var i=0,len=this.GoodsList.length;i<len;i++){
        if(this.GoodsList[i].goodsId == goods.goodsId){
            var delGoods=this.GoodsList.splice(i,1);
            this.deletedGoods.push(delGoods[0]);
        }
      }
    },
    reduce:function(goods){
      for(var i=0,len=this.GoodsList.length;i<len;i++){
        if(this.GoodsList[i].goodsId == goods.goodsId){
          this.GoodsList[i].count--;
          if(this.GoodsList[i].count == 0){
            this.GoodsList.splice(i,1);
          }
        }
      }
    },
    delAllGoods(){
      this.deletedGoods=this.deletedGoods.concat(this.GoodsList);
      this.GoodsList = [];
    }
  },
  watch:{
    GoodsList:{
        handler:function(val,old){
            this.goodsNumber = 0;
            this.goodsCount  = 0;
          for(var i=0, len = val.length;i<len;i++){
              this.goodsNumber += val[i].count;
              this.goodsCount += val[i].count*val[i].price;
          }
      },
      deep:true
    },
    deletedGoods:{
      handler:function(val,old){
       for(var i=0,len=val.length;i<len;i++){
        for(var j=i+1;j<len;j++){
          if(val[i].goodsId == val[j].goodsId){
            val[i].count += val[j].count;
            this.deletedGoods.splice(j,1);
          }
        }
       }
      },
      deep:true
    }
  },
  components:{
    NavRouter
  },
  mounted(){
    var clientH = document.body.clientHeight;
   document.getElementById('pos-order').style.height=clientH+'px';
  }
}
</script>
<style>
.Pos{
  width: 94%;
  float: left;
}
.pos-order {
  background: #ccc;
}
#pos-listGoods{
  background: #ddd;
}
.do {
  padding: 0 !important;
}
.payFor{
  margin-top: 20px;
  margin-left: 120px;
}
.GoodsDdetail{
  text-align: right;
  margin-top: 50px;
  color: #888;
}
.tableGoods{
  height: 400px;
  overflow: auto;
}
</style>



