import Vue from 'vue'
import Router from 'vue-router'
import Hot from '@/components/Hot'
import Single from '@/components/Single'
import Sweetmeat from '@/components/Sweetmeat'
import Refreshments from '@/components/Refreshments'
import Recommend from '@/components/Recommend'

Vue.use(Router)

export default new Router({
  history,
  routes: [
    {
      path: '/hot',
      name: 'hot',
      component: Hot
    },
    {
      path: '/single',
      name: 'single',
      component: Single
    },
    {
      path: '/sweetmeat',
      name: 'sweetmeat',
      component: Sweetmeat
    },
    {
      path: '/refreshments',
      name: 'refreshments',
      component: Refreshments
    },
    {
      path: '/recommend',
      name: 'recommend',
      component: Recommend
    },
    {
      path: '/',
      component: Hot
    }
  ]
})



